<template>
  <div id="app">
    <div class="set content">
      <h2>设置Base URL(请使用https)</h2>
      <!-- 由于Github Pages强制使用HTTPS，所以发送的Ajax请求必须为HTTPS加密的 -->
      <label>Base URL</label>
      <input v-model="inputBaseURL" v-focus ref="Input"><br>
      <!-- 这里设置好了，可以使用local storage存储下来 -->
      <button @click="setBaseURL(inputBaseURL)">set BaseURL</button>
    </div>

    <div class="send content">
      <button @click="test">测试后端连接</button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { mapMutations, mapState } from 'vuex'
export default {
  name: 'TestPage',
  data () {
    return {
      inputBaseURL: '',
      name: 'Dragon',
      password: '12345678'
    }
  },
  computed: {
    ...mapState('test', ['BaseURL'])
  },
  methods: {
    ...mapMutations('test', ['setBaseURL']),
    test () {
      axios({
        url: `${this.BaseURL}/test`,
        method: 'GET',
        // 设置超时请求世间为5s
        timeout: 5000
      }).then((response) => {
        console.log(response)
        // alert('后端访问成功，1s后即将跳转首页')
        this.$message({
          message: '后端访问成功，即将跳转首页',
          type: 'success',
          duration: 3000
        })
        setTimeout(() => {
          this.$router.push('/')
          console.log(this)
        }, 1000)
      }).catch((error) => {
        // 如果请求不成功，要么后端服务器没启动，要么BaseURL变了，所以要用户重新设置BaseURL
        alert('BaseURL已过期，请重新设置')
        console.log(error)
      })
    }
  },
  activated () {
    this.$refs.Input.focus()
  }
}
</script>

<style scoped lang="less">
#app {
  .content{
    text-align: center;
    line-height: 100px;
    font-weight: bold;
    font-size: 20px;
    h2{
      margin: 0;
    }
    label{
      display: inline-block;
      width: 150px;
    }
    button{
      font-weight: bold;
      border: 0;
      border-radius: 10px;
      width: 200px;
      height: auto;
      padding: 10px 10px;
      background-color: #42B983;
      font-size: 20px;
      color: white;
    }
    button:hover{
      background-color: #3498DB;
    }
    input{
      margin-right: 30px;
      width: 250px;
      height: auto;
      font-size: large;
      border: rgb(218, 218, 218) solid 1px;
      border-radius: 5px;
      padding: 8px;
    }
  }
  .set{
    margin-top: 0px;
  }
}
</style>
